import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Registration } from './registration';
import { Login } from './userclazz';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private http: HttpClient) { }

  findAll(): Observable<Registration[]>  {
    return this.http.get<any>('/register')
  }

  save(registration: Registration) {
    return this.http.post('/register', registration)
  }

  delete(iduser) {
    return this.http.delete('/register/' + iduser)
  }

  // isUserLoggedIn(){
  //   let user = sessionStorage.getItem('username')
  //   console.log(!(user === null))
  //   return !(user === null)
  // }

  savelogin(userclazz: Login): Observable<Login> {
    return this.http.post<any>('/userclazz', userclazz)
  }


}
