import { Injectable } from '@angular/core';
import { Allevent } from './allevent';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class AlleventService {

  constructor(private http: HttpClient) { }

  findAllallevents(): Observable<Allevent[]> {
    return this.http.get<any>('/allevent')
  }

  saveallevents(allevents: Allevent) {
    return this.http.post('/allevent', allevents)
  }

  deleteallevents(idevent) {
    return this.http.delete('/allevent/' + idevent)
  }
}
